.item2__text::before {
  content: "";
  position: absolute;
  bottom: 0;
  width: calc(100% - 30px);
  height: 2px;
}
.item2__text::after {
  content: "";
  position: absolute;
  bottom: 0;
  width: 2px;
  height: 100%;
}
.item2:nth-child(2n+1) .item2__body {
  left: 0;
  align-items: flex-start;
}
.item2:nth-child(2n+1) .item2__body .item2__header {
